.shopRow {
	display: -ms-flexbox;
	display: flex;
}

p {
	margin: 5px 0;
}

.card {
	-ms-flex: 1 1;
	    flex: 1 1;
	text-align: center;
	border: 3px solid #fff;
	padding: 30px;
}

.card:hover {
	border: 3px solid #000;
	cursor: pointer;
}

.cardLink > img {
	height: 400px;
	margin: 30px 0;
}

.card > a {
	color: #000;
	text-decoration: none;
}


