@font-face {
    font-family: avenir;
    src: url("../fonts/AvenirLTStd-Roman.otf");
}

@font-face {
    font-family: avenir_bold;
    src: url("../fonts/Avenir LT 95 Black.ttf");
}

html, body {
	margin: 0;
	padding: 0;
	font-family: avenir, sans-serif;
	overflow: hidden;
}

h1 {
	font-size: 4em;
	margin-bottom: 0;
	font-family: avenir_bold;
}

h3 {
	font-size: 2em;
	font-family: avenir_bold;
}

p {
	font-size: 1.2em;
}

.row {
	margin: 30px 0;
}

.right-align {
	text-align: right;
}

button {
	background-color: #000;
	border: 2px solid #FFF;
	color: white;
	padding: 10px 15px;
	font-size: 1.2em;
	cursor: pointer;
}

button:hover {
	background-color: #FFF;
	color: #000;
	border: 2px solid #000;
}

header {
	display: -ms-flexbox;
	display: flex;
	background-color: #80CBC4;
	color: white;
}

h2 {
	-ms-flex: 4 1;
	    flex: 4 1;
	padding: 0 40px;
}

.container {
	padding: 0 40px;
}

.shopping-cart {
	-ms-flex: 1 1;
	    flex: 1 1;
	display: -ms-flexbox;
	display: flex;
	padding: 13px;
	-ms-flex-direction: row-reverse;
	    flex-direction: row-reverse;
}

#cart {
	height: 35px;
}

#cart-count {
	margin: 10px 15px;
}

h2 > a, header > a {
	color: white;
	text-decoration: none;
}

h2 > a:hover,  header > a:hover {
	opacity: 0.8;
}

.return {
	color: #999;
	text-decoration: none;
}
.return:hover {
	opacity: 0.8;
}

.container_muffled {
	opacity: 0.3;
}

a:hover {
    cursor: pointer;
}

.addNotification {
    position: fixed;
    top: 80px;
    right: 10px;
    background-color: rgb(128, 203, 196);
    color: white;
	padding: 15px;
}

.headerArrow {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 10px 10px 10px;
    border-color: transparent transparent #80cbc4 transparent;
    position: fixed;
    top: 70px;
    right: 63px;
}

.invis {
	position: fixed;
	top: 0;
	right: 0;
	height: 300px;
	width: 300px;
}

.cartPreview {
	position: fixed;
    top: 80px;
    right: 10px;
    background-color: rgb(128, 203, 196);
    color: white;
	padding: 10px;
	max-height: 500px;
	overflow: auto;
}

.cartButton {
	z-index: 1;
}


