.cartContainer {
	display: -ms-flexbox;
	display: flex;
	height: 1000px;
	margin: 50px 0;
}

.cartContainer > div:first-child {
	-ms-flex: 1 1;
	    flex: 1 1;
}

.cartContainer > div {
	-ms-flex: 1 1;
	    flex: 1 1;
}

.item {
	display: -ms-flexbox;
	display: flex;
	margin: 50px 0;
	padding: 20px 50px;
}

.item-preview {
	display: -ms-flexbox;
	display: flex;
	padding: 20px;
	font-size: 12px;
}

.img, .img-preview {
	-ms-flex: 1 1;
	    flex: 1 1;
	text-align: center;
	margin-right: 50px;
}

.item-number {
	font-size: 1.5em;
	padding-top: 40px;
}

.text {
	-ms-flex: 2 1;
	    flex: 2 1;
}

.text-header, .text-footer {
	display: -ms-flexbox;
	display: flex;
}

.item-name {
	font-weight: 600;
	font-size: 1.5em;
	margin: 0;
	-ms-flex: 1 1;
	    flex: 1 1;
}

.item-price {
	-ms-flex: 1 1;
	    flex: 1 1;
	margin: 0;
}

.item-description {
	margin: 20px 0;
}

.img > img {
	height: 150px;
}

.img-preview > img {
	height: 100px;
}

.remove {
	-ms-flex: 1 1;
	    flex: 1 1;
	color: #999;
	font-size: 0.9em;
}
.remove:hover {
	cursor: pointer;
	opacity: 0.8;
}

.summary {
	padding: 0 50px;
	border: 1px solid #000;
	height: 700px;
}

.shoppingCart {
	height: 600px;
	overflow: auto;
}

.costs-item {
	display: -ms-flexbox;
	display: flex;
}

.costs-item > p {
	-ms-flex: 1 1;
	    flex: 1 1;
}

.total {
	font-weight: 600;
	font-size: 1.5em;
	position: fixed;
	top: 680px;
	right: 100px;
}

.checkout {
	position: fixed;
	top: 700px;
	right: 100px;
}

.quantity {
	-ms-flex: 1 1;
	    flex: 1 1;
}

.quantityInput {
	width: 30px;
	margin-left: 10px;
} 
.quantityInput:hover {
	opacity: 0.9;
}


